import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
// Import Containers
import { DefaultLayoutComponent } from './containers';
import { SchedulerComponent } from './views/scheduler/scheduler.component';
import { RotaComponent } from './views/rota/rota.component';

export const routes: Routes = [
  {
    path: '',
    redirectTo: 'dashboard',
    pathMatch: 'full',
  },
  {
    path: '',
    component: DefaultLayoutComponent,
    data: {
      title: 'Home'
    },
    children: [
      {
        path: 'dashboard',
        loadChildren: './views/dashboard/dashboard.module#DashboardModule'
      },
      {
        path: 'color-codes',
        loadChildren: './views/color-codes/color-codes.module#ColorCodesModule'
      },
      {
        path: 'site-codes',
        loadChildren: './views/site-codes/site-codes.module#SiteCodesModule'
      },
      {
        path: 'scheduler',
        loadChildren: './views/scheduler/scheduler.module#SchedulerModule'
      },
      {
        path: 'rota',
        loadChildren: './views/rota/rota.module#RotaModule'
      }
    ]
  },
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
