import { Injectable } from '@angular/core';
import {FormGroup , FormControl , Validators} from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import { MatTableDataSource , MatSort , MatPaginator } from '@angular/material';

@Injectable({
  providedIn: 'root'
})
export class ColorCodeService {

  form: FormGroup = new FormGroup({
    id : new FormControl(''),
    code : new FormControl('' , Validators.required),
    description : new FormControl('', Validators.required),
    colour_code: new FormControl('')
  })

  colors : MatTableDataSource<any>

  public messageSource: BehaviorSubject<MatTableDataSource<any>> = new BehaviorSubject<MatTableDataSource<any>>(this.colors);
  currentMessage = this.messageSource.asObservable();

  changeMessage(message: MatTableDataSource<any>) {
    this.messageSource.next(message)
  }



  initializeFormgroup()
  {
    this.form.setValue({
      id : '',
      code : '',
      description : '',
      colour_code : ''
    })
  }

  constructor(private http : HttpClient) { }

 

  save_color_status(color_status)
  {
      this.http.post(`http://localhost:3002/create-status-code` ,{color_status} )
      .subscribe(res=> {
        console.log(res)
      });
  }

  update_color_status(color_status)
  {
    this.http.post(`http://localhost:3002/update-status-code` ,{color_status} )
      .subscribe(res=> {
        console.log(res)
      });
  }

  getColorList()
  {
  return this.http.get(`http://localhost:3002/color_status`)
  }

  populateform(formvalue)
  {
    this.form.setValue(formvalue);
  }

  delete_status(color_status)
  {
    this.http.post(`http://localhost:3002/delete-status-code` ,{color_status} )
      .subscribe(res=> {
        console.log(res)
      });
  }

}
